* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.list--info {
  display: flex;
  align-items:  center;
}

.list--info__item {
  width: 35px;
  height: 20px;
  margin-right: 20px;
}

input{
  margin-left: 40px;
}

.country-info__item{
  width: 50px;
  height: 40px;
  margin-right: 20px;
  margin-left: 40px;
}

.country{
  display: flex;
  align-items:  center;
}

.list{
list-style: none;
line-height: 35px;
}
